import { getVirtualHostOptions } from '../common/component-handler/press-component';
import { addUnit } from '../common/utils/add-unit';
import styleUtil from '../common/utils/style';
import { isNotInUni } from '../common/utils/utils';


import InnerImage from '../image/index.vue';

import PressIcon from '../press-icon/press-icon.vue';
import PressTransition from '../press-transition/press-transition.vue';

import props from './computed';


export default {
  name: 'PressImage',
  options: {
    ...getVirtualHostOptions(true, false),
    styleIsolation: 'shared',
  },
  components: {
    PressIcon,
    PressTransition,

    InnerImage,

  },
  mixins: [props],
  emits: [
    'click',
    'error',
    'load',
  ],
  data() {
    return {
      // 图片是否加载错误，如果是，则显示错误占位图
      isError: false,
      // 初始化组件时，默认为加载中状态
      loading: true,
      // 不透明度，为了实现淡入淡出的效果
      opacity: 1,
      // 过渡时间，因为props的值无法修改，故需要一个中间值
      durationTime: this.duration,
      // 图片加载完成时，去掉背景颜色，因为如果是png图片，就会显示灰色的背景
      backgroundStyle: {},
      // 用于fade模式的控制组件显示与否
      show: false,

      isNotInUni: isNotInUni(),
    };
  },
  computed: {
    wrapStyle() {
      const style = {};
      style.width = addUnit(this.width);
      style.height = addUnit(this.height);

      // 如果是显示圆形，设置一个很多的半径值即可
      style.borderRadius = this.round ? '10000px' : addUnit(this.radius);

      // 如果设置圆角，必须要有 hidden，否则可能圆角无效
      style.overflow = this.radius > 0 ? 'hidden' : 'visible';

      const res = styleUtil([
        {
          ...style,
          ...this.backgroundStyle,
        },
        this.customStyle,
      ]);

      return res;
    },
  },
  watch: {
    src: {
      immediate: true,
      handler(n) {
        if (!n) {
          // 如果传入null或者''，或者false，或者undefined，标记为错误状态
          this.isError = true;
        } else {
          this.isError = false;
          this.loading = true;
        }
      },
    },
  },
  mounted() {
    this.show = true;
  },
  methods: {
    addUnit,
    // 点击图片
    onClick() {
      this.$emit('click');
    },
    // 图片加载失败
    onErrorHandler(err) {
      this.loading = false;
      this.isError = true;
      this.$emit('error', err);
    },
    // 图片加载完成，标记loading结束
    onLoadHandler(event) {
      this.loading = false;
      this.isError = false;
      this.$emit('load', event);
      this.removeBgColor();
    },
    // 移除图片的背景色
    removeBgColor() {
      // 淡入动画过渡完成后，将背景设置为透明色，否则png图片会看到灰色的背景
      this.backgroundStyle = {
        backgroundColor: 'transparent',
      };
    },
  },
};
